.button {
    button {
        cursor: pointer;
        border: none;
        outline: none;
        width: 426px;
        height: 68px;
        border-radius: 35px;
        letter-spacing: 1px;
        font-size: 29px;
        font-weight: 700;
        color: #fff;
        background-color: #FF9514;
    }

    :hover {
        background-color: #000;
    }

    :disabled {
        opacity: 0.5;
    }
}

@media screen and (max-width: 768px) {
    .button {
        button {
            width: 310px;
        }
    }
}